import type { MouseEvent, TouchEvent, FC } from 'react'
import { cn } from '@utils/ui'
import { createRipple } from '@utils/ripple'
import CLASS_NAME from './styles'

export enum IconButtonVariant {
  DARK = 'dark',
  LIGHT = 'light'
}

export interface IconButtonProps {
  className?: string
  dataTestId?: string
  onClick?: (e?: MouseEvent<HTMLElement>) => void
  variant?: IconButtonVariant
}

const IconButton: FC<IconButtonProps> = ({
  children,
  className,
  dataTestId,
  onClick,
  variant = 'dark'
}) => {
  const onMouseDown = (e: MouseEvent<HTMLElement> | TouchEvent<HTMLElement>): void => {
    createRipple(e)
  }

  return (
    <button
      className={cn(CLASS_NAME, className, `cf-ripple-container-${variant}`, 'cf-icon-button')}
      data-test-id={dataTestId}
      onClick={onClick}
      onMouseDown={onMouseDown}
      onTouchStart={onMouseDown}
    >
      {children}
    </button>
  )
}

export default IconButton
